<script>
import lottie from "lottie-web";
import tabJSON from "@/assets/json/tab.json"; // 引入json文件
import huanxingJson from "@/assets/json/huangxing.json"; // 引入json文件
// import potJSON from "@/assets/json/pot1-1.json"; // 引入json文件
// import potHoverJson from "@/assets/json/pot2.json"; // 引入json文件

export default {
  name: "chart-base",
  props: {
    data: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      show: false,
      instance: null,
      ready: false,
      options: {
        autoplay: true,
        loop: true
      },
      bgCircleTop: 0,
      carouselTimer: null,
      carouselPropList: [
        { title: "NB-IoT Meter Customers", prop: "customerNum" },
        { title: "Qty. of Opened Account", prop: "meterOpenNum" },
        { title: "Gas Consumption", prop: "gasAmount" }
      ],
      areaList: [
        "northwest",
        "southwest",
        "southChina",
        "centralChina",
        "eastChina",
        "northChina",
        "northeast"
      ],
      carouselIndex: 0,
      showDetailIndex: 0,
      carouselCur: { title: "NB-IoT Meter Customers", prop: "customerNum" },
      generalIntervalTime: 3000,
      detailIntervalTime: 5000,
      areaDetailInfo: {},
      areaCur: "",
      areaImg: null,
      showDetailInfo: false,
      chinaMapHeight: "",
      tabLottie: null,
      circleLottie: null,
      potLottie: null
    };
  },
  mounted() {
    //开启定时器
    this.tabLottie = lottie.loadAnimation({
      animationData: tabJSON,
      loop: true,
      autoplay: true,
      renderer: "svg",
      container: this.$refs.areaContainer // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
    });
    this.tabLottie.play();
    //地图底部的环形
    this.circleLottie = lottie.loadAnimation({
      animationData: huanxingJson,
      loop: true,
      autoplay: true,
      renderer: "svg",
      container: this.$refs.circleBottom // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
    });
    this.circleLottie.play();
    //区域选中的指示圈
    // this.potLottie =  lottie.loadAnimation({
    //         animationData:potJSON,
    //         loop:true,
    //         autoplay:true,
    //         renderer:'svg',
    //         container: this.$refs.circleBottom, // 容器 //渲染方式，有"html"、"canvas"和"svg"三种
    //   });
    //   this.potLottie.play()
    setTimeout(() => {
      this.chinaMapHeight = this.$refs.chinaMap.offsetHeight;
      this.showGeneralInfoTimer();
      this.showDetailInfoTimer();
    }, 3000);
  },
  methods: {
    showGeneralInfoTimer() {
      this.generalTimer = setInterval(() => {
        if (this.carouselIndex >= 2) {
          this.carouselIndex = 0;
        } else {
          this.carouselIndex++;
        }
        this.carouselCur = this.carouselPropList[this.carouselIndex];
      }, this.generalIntervalTime);
    },
    showDetailInfoTimer() {
      this.detailTimer = setInterval(() => {
        if (this.showDetailIndex >= 6) {
          this.showDetailIndex = 0;
        } else {
          this.showDetailIndex++;
        }
        var area = this.areaList[this.showDetailIndex];
        this.areaCur = area;
        this.showDetailInfo = false;
        this.areaDetailInfo = this.data[area];
        this.$nextTick(() => {
          //带有背景的文字，文字改变了，背景不改变，v-if触发重新渲染css
          this.showDetailInfo = true;
        });
      }, this.detailIntervalTime);
    },
    stopTimer(timer) {
      clearInterval(this[timer]);
      this[timer] = null;
    },
    hoverArea(area) {
      if (this.areaCur == area) {
        return;
      } else {
        this.stopTimer("detailTimer");
        this.showDetailIndex = this.areaList.findIndex(item => {
          return item == area;
        });
        var area = this.areaList[this.showDetailIndex];
        this.areaCur = area;
        this.showDetailInfo = false;
        this.areaDetailInfo = this.data[area];
        this.$nextTick(() => {
          //带有背景的文字，文字改变了，背景不改变，v-if触发重新渲染css
          this.showDetailInfo = true;
        });
        this.showDetailInfoTimer();
      }
    },
    unHoverArea() {
      this.areaCur = "";
      this.showDetailInfo = false;
      this.showGeneralInfoTimer();
    }
  },
  beforeDestroyed() {
    this.stopTimer("generalTimer");
    this.stopTimer("detailTimer");
    this.tabLottie.destory();
    this.circleLottie.destory();
    //  this.potLottie.destory();
  }
};
</script>

